import React, { Component } from 'react';
import {
  Page,
  Navbar,
  List,
  ListItem,
  ListInput,
  Link,
  Tabs,
  Toolbar,
  Tab,
  Range,
  Block,
  Icon, Fab
} from 'framework7-react';
import { dict } from '../../Dict';
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import SectionShow from "../../containers/sections/show"

export default class Layout extends Component {
  constructor() {
    super();
    this.getInstance = this.getInstance.bind(this);
    
    this.state = {
      token: window.localStorage.getItem('token'),
      faculties: null,
      assets:  null,
      total_assets: null,
      resource: null,
      quiz: null,
      assignment: null,
      onlineCourseCount: null,
      onlineCourseDuration: null,
      activity: null,
      id: null,
      name: null,
      bbOnlineCourseCount: null,
      bbOnlineCourseDuration: null,
    }
  }

  componentWillMount() {
    ModelStore.on("got_instance", this.getInstance);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_instance", this.getInstance);
  }

  componentDidMount() {
    MyActions.getInstance('courses/sections', this.$f7route.params['sectionId'], this.state.token);
  }

  getInstance() {
    var section = ModelStore.getIntance()
    var klass = ModelStore.getKlass()
    if (section && klass === 'Section') {
      this.setState({
        faculties: section.section_faculties,
        assets: section.section_assets,
        id: section.id,
        name: section.name,
        quiz: section.most_quiz,
        total_assets: section.total_assets,
        resource: section.most_resource,
        assignment: section.most_assignment,
        activity: section.most_activity,
        onlineCourseCount: section.most_online_course_count,
        onlineCourseDuration: section.most_online_course_duration,
        bbOnlineCourseCount: section.most_bb_online_course_count,
        bbOnlineCourseDuration: section.most_bb_online_course_duration,
      });
    }
    console.log(section)    
  }



  render() {
    const { 
      id, faculties, assets, total_assets , quiz, name,
      resource, assignment, onlineCourseCount, onlineCourseDuration, 
      activity, bbOnlineCourseCount, bbOnlineCourseDuration} = this.state;
    return (
      <Page>
        <Navbar title={dict.sections} backLink={dict.back} />
        <SectionShow 
          id={id} faculties={faculties} name={name} assignment={assignment} 
          onlineCourseCount={onlineCourseCount} activity={activity} 
          onlineCourseDuration={onlineCourseDuration} quiz={quiz} 
          resource={resource} assets={assets} total_assets={total_assets}
          bbOnlineCourseCount={bbOnlineCourseCount}
          bbOnlineCourseDuration={bbOnlineCourseDuration}
          />
      </Page>
    );
  }
}
