import React from "react"
import { Page,Fab, Icon } from 'framework7-react';
import ModelStore from "../../stores/ModelStore";
import SectionIndex from "../../containers/sections/index"
import * as MyActions from "../../actions/MyActions";
import { dict} from '../../Dict';
import Framework7 from 'framework7/framework7.esm.bundle';


export default class Layout extends React.Component {
  constructor() {
    super();
    this.getList = this.getList.bind(this);
    this.search = this.search.bind(this);
    
    this.state = {
      token: window.localStorage.getItem('token'),
      sections: null,
      query: null,
    }
  }
  componentWillMount() {
    ModelStore.on("got_list", this.getList);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_list", this.getList);
  }

  componentDidMount(){
    this.loadData();
  }

  loadData(){
    MyActions.getList('courses/sections', this.state.page, {}, this.state.token);
  }

  search(obj){
    this.setState(obj, () => {
      MyActions.getList('sections/search', this.state.page, {q: this.state.query}, this.state.token);
  });    
  }

  getList() {
    var sections = ModelStore.getList()
    var klass = ModelStore.getKlass()
    if (sections && klass === 'Section'){
      this.setState({
        sections: sections,
      });
      console.log(sections)
    }
  }

  render() {
    const {sections} = this.state;
    return(<SectionIndex sections={sections} search={this.search}/>)
  }
}
